.about {
    flex-grow: 1;
}

.about p {
    font-size: 18px;
    font-style: normal;
    color: #61727D; 
    text-align: center;     
}

.about {
    margin-bottom: 20px;
    color: #99B4C5;
    text-align: center;   
}

.about .chip {
    display: inline-block;
    padding: 0 15px;
    height: 50px;
    font-size: 16px;
    line-height: 50px;
    border-radius: 25px;
    background-color: #D3BBED;
    color:#ffffff;;
    cursor: pointer;
    margin: 0 5px 3px 0

}

.about .info{
    width: 100%;
    
}

.about .info img{
    width: 80%;
    border-radius: 300px;
    
}

@media (min-width: 800px){
    .about .info img{
        width: 15%;
    }
}






